import React from 'react';

class ListBody extends React.Component {
  render() {
    let itemData = this.props.itemData;
    return (
      <>
        {itemData.map((itemData) => {
          return (
            <div>
              {itemData.title}
              {itemData.cost}
              {itemData.num}
            </div>
          );
        })}
      </>
    );
  }
}

export default ListBody;
